import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
export default new Router({
  //mode: 'history',
  routes: [
    {
      path: '/',
      component:  resolve => require(['../views/home/index.vue'], resolve),
      meta: { title: 'QIMAIKE-首页' },
    },
    {
      path: '/about',
      component:  resolve => require(['../views/about/index.vue'], resolve),
      meta: { title: 'QIMAIKE-关于我' },
    },
    {
      path: '/works',
      component:  resolve => require(['../views/works/index.vue'], resolve),
      meta: { title: 'QIMAIKE-案例' },
    },
    {
      path: '/worksDetail/:id',
      component:  resolve => require(['../views/works/list.vue'], resolve),
      meta: { title: 'QIMAIKE-案例详情' },
    }
  ]
})



